}


.youtube  .youtube__area{
  width:1920px;
  background-color: orange;
  position: absolute;
  left:50%;
  margin-left: calc(1920px / -2); 
  top:50%;
  margin-top: calc(1080px /-2); 
}


.youtube  .youtube__area::before {
  content:"";
  display: block;
  width:100%;
  height:0;
